// @flow
import React from 'react';
import styles from './styles.css';

const UploadIcon = () => (
  <i
    className={`fa fa-arrow-circle-up ${styles.gradientFill} ${styles.uploadIcon}`}
    aria-hidden="true"
  />
);

export default UploadIcon;



